"use client"

import { useState } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ArrowRight, Check, Sparkles } from "lucide-react"

export function WaitlistForm() {
    const [email, setEmail] = useState("")
    const [status, setStatus] = useState<"idle" | "loading" | "success">("idle")
    const [error, setError] = useState(false)

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        if (!email || !email.includes("@")) {
            setError(true)
            return
        }

        setError(false)
        setStatus("loading")

        // Simulate API call
        setTimeout(() => {
            setStatus("success")
            setEmail("")
        }, 1500)
    }

    return (
        <div className="w-full max-w-sm mx-auto">
            <AnimatePresence mode="wait">
                {status === "success" ? (
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.95 }}
                        className="flex flex-col items-center justify-center p-8 text-center bg-white/5 border border-white/10 rounded-3xl backdrop-blur-2xl shadow-[0_0_40px_-10px_rgba(0,0,0,0.5)]"
                    >
                        <div className="w-16 h-16 bg-gradient-to-tr from-green-500/20 to-emerald-500/20 rounded-full flex items-center justify-center mb-6 ring-1 ring-white/10">
                            <Check className="w-8 h-8 text-emerald-400" />
                        </div>
                        <h3 className="text-2xl font-serif font-medium text-white mb-2">You're on the list.</h3>
                        <p className="text-neutral-400">Watch your inbox. We open the doors soon.</p>
                    </motion.div>
                ) : (
                    <motion.form
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -20 }}
                        onSubmit={handleSubmit}
                        className="relative flex flex-col gap-4 p-2 rounded-2xl md:p-8 md:bg-white/5 md:backdrop-blur-xl md:border md:border-white/10 md:shadow-[0_0_40px_-10px_rgba(0,0,0,0.5)]"
                    >
                        <div className="md:hidden mb-4 text-center">
                            <span className="text-sm text-neutral-400">Join the exclusive waitlist</span>
                        </div>

                        <div className="relative">
                            <Input
                                type="email"
                                placeholder="enter your email"
                                value={email}
                                onChange={(e) => {
                                    setEmail(e.target.value)
                                    if (error) setError(false)
                                }}
                                error={error}
                                className="h-14 bg-black/40 border-white/10 focus:border-white/20 placeholder:text-neutral-600 text-lg"
                            />
                        </div>
                        <Button
                            type="submit"
                            isLoading={status === "loading"}
                            size="lg"
                            className="w-full text-base font-medium shadow-none hover:shadow-lg hover:shadow-electric-blue/20"
                        >
                            Request Access
                        </Button>

                        <div className="flex items-center justify-center gap-2 mt-2">
                            <span className="relative flex h-2 w-2">
                                <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                                <span className="relative inline-flex rounded-full h-2 w-2 bg-emerald-500"></span>
                            </span>
                            <p className="text-xs text-neutral-500">
                                <span className="text-neutral-300 font-medium">Waitlist is moving fast.</span> Secure your spot.
                            </p>
                        </div>
                    </motion.form>
                )}
            </AnimatePresence>
        </div>
    )
}
