import * as React from "react"
import { cn } from "@/lib/utils"
// import { Loader2 } from "lucide-react" // We'll use a custom SVG for smoother animation or lucide if installed

export interface ButtonProps
    extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    variant?: "primary" | "secondary" | "outline" | "ghost"
    size?: "default" | "sm" | "lg" | "icon"
    isLoading?: boolean
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
    ({ className, variant = "primary", size = "default", isLoading, children, ...props }, ref) => {
        return (
            <button
                ref={ref}
                disabled={isLoading || props.disabled}
                className={cn(
                    "relative inline-flex items-center justify-center rounded-lg font-medium transition-all duration-300 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-electric-blue focus-visible:ring-offset-2 focus-visible:ring-offset-black disabled:pointer-events-none disabled:opacity-50 active:scale-95",
                    {
                        "bg-white text-black hover:bg-neutral-200 shadow-[0_0_20px_-5px_rgba(255,255,255,0.3)] hover:shadow-[0_0_25px_-5px_rgba(255,255,255,0.5)]": variant === "primary",
                        "bg-neutral-900/50 text-white border border-neutral-800 hover:bg-neutral-900 hover:border-neutral-700 backdrop-blur-sm": variant === "secondary",
                        "border border-white/10 bg-transparent hover:bg-white/5 text-white": variant === "outline",
                        "hover:bg-white/5 text-neutral-300 hover:text-white": variant === "ghost",
                        "h-10 px-4 py-2 text-sm": size === "default",
                        "h-9 px-3 rounded-md text-xs": size === "sm",
                        "h-12 px-8 rounded-xl text-base": size === "lg",
                        "h-10 w-10": size === "icon",
                    },
                    className
                )}
                {...props}
            >
                {isLoading && (
                    <span className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2">
                        <svg className="animate-spin h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                    </span>
                )}
                <span className={cn("flex items-center gap-2", isLoading && "invisible")}>
                    {children}
                </span>
            </button>
        )
    }
)
Button.displayName = "Button"

export { Button }
