"use client";

import { motion } from "framer-motion";
import { WaitlistForm } from "./waitlist-form";
import { cn } from "@/lib/utils";

export function Hero() {
    return (
        <section className="relative flex min-h-screen flex-col items-center justify-center overflow-hidden px-4 py-24 text-center">

            {/* Badge */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, ease: "easeOut" }}
                className="mb-8 inline-flex items-center gap-2 rounded-full border border-white/5 bg-white/5 px-3 py-1 text-xs text-neutral-300 backdrop-blur-xl hover:bg-white/10 transition-colors cursor-default"
            >
                <span className="flex h-2 w-2">
                    <span className="absolute h-2 w-2 animate-ping rounded-full bg-electric-blue opacity-75"></span>
                    <span className="relative inline-flex h-2 w-2 rounded-full bg-electric-blue"></span>
                </span>
                <span className="font-medium tracking-wide">v0.1 // PRIVATE BETA</span>
            </motion.div>

            {/* Main Headline */}
            <motion.h1
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.1, ease: "easeOut" }}
                className="max-w-4xl bg-gradient-to-br from-white via-white to-neutral-400 bg-clip-text text-5xl font-bold tracking-tighter text-transparent sm:text-7xl md:text-8xl lg:text-9xl"
            >
                Stop AI <br className="hidden md:block" /> <span className="font-serif italic text-white drop-shadow-[0_0_30px_rgba(255,255,255,0.3)]">Amnesia.</span>
            </motion.h1>

            {/* Subheadline */}
            <motion.p
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.2, ease: "easeOut" }}
                className="mt-6 max-w-2xl text-lg text-neutral-400 sm:text-xl font-light"
            >
                The browser extension that teleports context between your chats.
                Move seamlessly from ChatGPT to Gemini without ever repeating yourself.
            </motion.p>

            {/* CTA / Form */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.3, ease: "easeOut" }}
                className="mt-10 w-full"
            >
                <WaitlistForm />

                {/* Micro-copy */}
                <div className="mt-8 flex flex-col items-center gap-2 text-xs text-neutral-500 font-mono opacity-80">
                    <p>Compatible with: ChatGPT, Gemini, Claude.</p>
                    <p className="flex items-center gap-1.5">
                        <span className="relative flex h-1.5 w-1.5">
                            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                            <span className="relative inline-flex rounded-full h-1.5 w-1.5 bg-emerald-500"></span>
                        </span>
                        Status: Deploying...
                    </p>
                </div>
            </motion.div>

        </section>
    );
}
