import * as React from "react"
import { cn } from "@/lib/utils"

export interface InputProps
    extends React.InputHTMLAttributes<HTMLInputElement> {
    error?: boolean
}

const Input = React.forwardRef<HTMLInputElement, InputProps>(
    ({ className, type, error, ...props }, ref) => {
        return (
            <div className="relative group">
                <div className={cn(
                    "absolute -inset-0.5 bg-gradient-to-r from-neutral-800 to-neutral-700 rounded-xl blur opacity-25 group-hover:opacity-50 transition duration-1000 group-hover:duration-200",
                    error && "from-red-500/50 to-red-900/50 opacity-50"
                )}
                />
                <input
                    type={type}
                    className={cn(
                        "relative flex h-12 w-full rounded-xl border border-neutral-800 bg-neutral-900/90 px-4 py-2 text-sm text-neutral-100 file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-neutral-500 focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-neutral-400 focus-visible:border-neutral-700 disabled:cursor-not-allowed disabled:opacity-50 transition-all duration-300 shadow-[inset_0_1px_1px_rgba(255,255,255,0.05)]",
                        error && "border-red-500/50 focus-visible:ring-red-500/50",
                        className
                    )}
                    ref={ref}
                    {...props}
                />
            </div>
        )
    }
)
Input.displayName = "Input"

export { Input }
